<template>
  <div>
    <div class="container">
    <div class="row">
      <div class="col s12 m8 offset-m2">
        <div class="login card-panel blue white-text center">
          <h3>Iniciar sesión</h3>
          <form action="index.html">
            <div class="input-field">
              <i class="material-icons prefix">email</i>
              <input type="email" id="email" v-model="email">
              <label class="white-text" for="email">Correo electrónico</label>
            </div>
            <div class="input-field">
              <i class="material-icons prefix">lock</i>
              <input type="password" id="password" v-model="password">
              <label class="white-text" for="password">Contraseña</label>
            </div>
            <button v-on:click="login" class="btn btn-large btn-extended grey lighten-4 black-text">Iniciar sesión</button>
            <button v-on:click="cancelarLogin" class="btn btn-large btn-extended grey lighten-4 black-text">Cancelar</button>
          </form>
        </div>
      </div>
    </div>
  </div>
  </div>

</template>

<script>
import firebase from 'firebase';

export default {
  name: 'login',
  data: function() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    login: function(e) {

      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          user => {

            //alert(`You are logged in as ${user.email}`);

            debugger;
            this.$router.go({ path: this.$router.path });

          },
          err => {
            debugger;

            alert(err.message);
          }
        );
      e.preventDefault();

    },

    cancelarLogin(){


    }

  }
};
</script>



