<template>

  <div>
    <div class="card" v-if="!listar">
      <div class="card">
        <div class="card-header text-center">
          <h3>Datos de las personas</h3>
        </div>
      </div>
      <div class="card-body">

        <form>
          <div class="form-group">
            <label>Nombres: </label>
            <input class="form-control" placeholder="Nombres" type="text" v-model="persona.nombres"/>
          </div>
          <div class="form-group">
            <label>Apellidos: </label>
            <input class="form-control" placeholder="Apellidos" type="text" v-model="persona.apellidos"/>
          </div>
          <div class="form-group">
            <label>Correo: </label>
            <input class="form-control" placeholder="Correo" type="email" v-model="persona.email"/>
          </div>
          <div v-if="!edit" class="form-group">
            <button type="button" class="btn blue" @click="agregarPreinscrito()">Agregar</button>
            <button type="button" class="btn yellow" @click="cancelarAgregar()">Cancelar</button>
          </div>
          <div v-else>
            <button type="button" class="btn blue" @click="editarPreinscrito()">Editar</button>
            <button type="button" class="btn yellow" @click="cancelarEditar()">Cancelar</button>
          </div>
        </form>
      </div>
    </div>

    <div class="card" v-else>
      <div class="card-header">
          <h3 class="cad-title text-center">Usuarios Preinscritos</h3>
      </div>
      <div class="card-body">

          <div class="fixed-action-btn">
            <a class="btn-floating btn-large green" @click="agregar()">
              <i class="fa fa-plus"></i>
            </a>
          </div>

          <table class="table table-striped responsive-table">
            <thead>
              <tr>
                <th>Nombre</th>
                <th>Correo</th>
                <th>Acción</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="persona of datosPreinscritos" v-bind:key="persona['.key']">
                <td>
                  <p>{{ persona.nombres }} {{persona.apellidos}}</p>
                </td>
                <td>
                  <p>{{ persona.email }}</p>
                </td>
                <td>
                  <div>
                    <button class="btn btn-floating blue" @click="editar(persona)">
                      <i class="fa fa-pencil-square-o" aria-hidden="true"></i> Editar
                    </button>

                    <button class="btn btn-floating red" @click="borrarPreinscrito(persona['.key'])">
                      <i class="fa fa-trash-o" aria-hidden="true"></i> Borrar
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
      </div>
    </div>

    <v-dialog/>
  </div>

</template>

<script>
import { preinscritosRef } from "./firebaseInit";
import toastr from "toastr";
import VModal from "vue-js-modal";

export default {
  name: "Preinscritos",

  data() {
    return {
      persona: {
        nombres: "",
        apellidos: "",
        email: "",
        key: ""
      },
      edit: false,
      listar: true
    };
  },
  firebase: {
    datosPreinscritos: preinscritosRef
  },

  methods: {
    agregar() {
      this.edit = false;
      this.listar = false;
    },

    cancelarAgregar() {
      this.listar = true;
    },

    agregarPreinscrito() {
      preinscritosRef.push({
        nombres: this.persona.nombres,
        apellidos: this.persona.apellidos,
        email: this.persona.email
      });
      this.persona.nombres = "";
      this.persona.apellidos = "";
      this.persona.email = "";
      this.persona.key = "";
      this.listar = true;
      toastr.success("Persona agregada a la preincripción");
    },

    borrarPreinscrito(key) {
      this.$modal.show("dialog", {
        title: "Confirmación!",
        text: "¿Está seguro que quiere borrar este registro?",
        buttons: [
          {
            title: "Aceptar",
            handler: () => {
              preinscritosRef.child(key).remove();
              toastr.success("Persona retirada de la preincripción");
            },
            closed: true
          },
          {
            title: "Cencelar"
          }
        ]
      });
    },

    editar(datos) {
      this.edit = true;
      this.listar = false;
      this.persona.nombres = datos.nombres;
      this.persona.apellidos = datos.apellidos;
      this.persona.email = datos.email;
      this.persona.key = datos[".key"];
    },

    editarPreinscrito(key) {
      preinscritosRef.child(this.persona.key).update({
        nombres: this.persona.nombres,
        apellidos: this.persona.apellidos,
        email: this.persona.email
      });

      this.persona.nombres = "";
      this.persona.apellidos = "";
      this.persona.email = "";
      this.persona.key = "";
      toastr.success("Datos actualizados correctamente.");
      this.edit = false;
      this.listar = true;
    },

    cancelarEditar() {
      this.edit = false;
      this.listar = true;
    }
  }
};
</script>



