<template>

 <div>
    <div class="card" v-if="!listar">
      <div class="card">
        <div class="card-header text-center">
          <h3>Datos de la sala</h3>
        </div>
      </div>
      <div class="card-body">

        <form>
          <div class="form-group">
            <label>Nombre: </label>
            <input class="form-control" placeholder="Nombres" type="text" v-model="sala.nombre"/>
          </div>
          <div class="form-group">
            <label>Capacidad: </label>
            <input class="form-control" placeholder="Apellidos" type="text" v-model="sala.capacidad"/>
          </div>

          <div v-if="!edit" class="form-group">
            <button type="button" class="btn blue" @click="agregarPreinscrito()">Agregar</button>
            <button type="button" class="btn yellow" @click="cancelarAgregar()">Cancelar</button>
          </div>
          <div v-else>
            <button type="button" class="btn blue" @click="editarPreinscrito()">Editar</button>
            <button type="button" class="btn yellow" @click="cancelarEditar()">Cancelar</button>
          </div>
        </form>
      </div>
    </div>

    <div class="card" v-else>
      <div class="card-header">
          <h3 class="cad-title text-center">Salas disponibles</h3>
      </div>
      <div class="card-body">

          <!-- <div class="fixed-action-btn">
            <a class="btn-floating btn-large green" @click="agregar()">
              <i class="fa fa-plus"></i>
            </a>
          </div> -->

          <table class="table table-striped responsive-table">
            <thead>
              <tr>
                <th>Nombre</th>
                <th>Capacidad</th>
                <th>Acción</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="sala of datosSalasDisponibles" v-bind:key="sala['.key']">
                <td>
                  <p>{{ sala.nombre }}</p>
                </td>
                <td>
                  <p>{{ sala.capacidad }}</p>
                </td>
                <td>
                  <div>
                    <button class="btn btn-floating blue" @click="editar(sala)">
                      <i class="fa fa-pencil-square-o" aria-hidden="true"></i> Editar
                    </button>

                    <button class="btn btn-floating red" @click="borrarSalas(sala['.key'])">
                      <i class="fa fa-trash-o" aria-hidden="true"></i> Borrar
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
      </div>
    </div>
    <v-dialog/>
  </div>

</template>

<script>

import { salasRef } from './firebaseInit';
import toastr from "toastr";
import VModal from 'vue-js-modal';

export default{

  name: 'Salas',

  data () {
    return {

      sala: {
        nombre: "",
        capacidad: "",
        key: ""
      },
      edit: false,
      listar: true

    }
  },
  firebase: {
    datosSalasDisponibles: salasRef
  },

  methods: {

    // agregar() {
    //   this.edit = false;
    //   this.listar = false;
    // },

    // cancelarAgregar() {
    //   this.listar = true;
    // },

    // agregarPreinscrito() {
    //   salasRef.push({
    //     nombres: this.persona.nombres,
    //     apellidos: this.persona.apellidos,
    //     email: this.persona.email
    //   });
    //   this.persona.nombres = "";
    //   this.persona.apellidos = "";
    //   this.persona.email = "";
    //   this.persona.key = "";
    //   this.listar = true;
    //   toastr.success("Persona agregada a la preincripción");
    // },

    borrarSalas(key) {
      this.$modal.show('dialog', {
      title: 'Confirmación!',
      text: '¿Está seguro que quiere borrar este registro?',
      buttons: [
        {
          title: 'Aceptar',
          handler: () => {
            salasRef.child(key).remove();
            toastr.success("Sala eliminada");
          },
          closed: true
        },
        {
          title: 'Cencelar'
        }
      ]
      })
    },

    editar(datos) {
      this.edit = true;
      this.listar = false;
      this.sala.nombre = datos.nombre;
      this.sala.capacidad = datos.capacidad;
      this.sala.key = datos[".key"];
    },

    editarPreinscrito(key) {
      salasRef.child(this.sala.key).update({
        nombre: this.sala.nombre,
        capacidad: this.sala.capacidad,
      });

      this.sala.nombre = "";
      this.sala.capacidad = "";
      this.sala.key = "";
      toastr.success("Datos actualizados correctamente.");
      this.edit = false;
      this.listar = true;
    },

    cancelarEditar(){
      this.edit = false;
      this.listar = true;
    }

  }

}

</script>



