import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import Preinscritos from '@/components/Preinscritos'
import Salas from '@/components/Salas'
import Programacion from '@/components/Programacion'
import ViewAgenda from '@/components/ViewAgenda'

import Login from '@/components/Login';
import firebase from 'firebase';

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/preinscritos',
      name: 'preinscritos',
      component: Preinscritos,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/salas',
      name: 'salas',
      component: Salas,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/programacion',
      name: 'programacion',
      component: Programacion,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/agenda/:evento_id',
      name: 'view-agenda',
      component: ViewAgenda,
      meta: {
        requiresAuth: true
      }
    }

  ]
});


// Nav Guard
router.beforeEach((to, from, next) => {
  // Check for requiresAuth guard
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // Check if NO logged user
    if (!firebase.auth().currentUser) {
      // Go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // Proceed to route
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    // Check if NO logged user
    if (firebase.auth().currentUser) {
      // Go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // Proceed to route
      next();
    }
  } else {
    // Proceed to route
    next();
  }
});

export default router;



