<template>
  <div id="view-agenda">

    <div>

    <div class="card" v-if="!listar">
      <div class="card">
        <div class="card-header text-center">
          <h3>Datos de la agenda</h3>
        </div>
      </div>
      <div class="card-body">

        <form>
          <div class="form-group">
            <label>Nombre: </label>
            <input class="form-control" placeholder="Nombres" type="text" v-model="datosAgenda.nombre"/>
          </div>
          <div class="form-group">
            <label>Lugar: </label>
            <input class="form-control" placeholder="Lugar" type="text" v-model="datosAgenda.lugar"/>
          </div>
          <div class="form-group">
            <label>Ponente: </label>
            <input class="form-control" placeholder="Ponente" type="text" v-model="datosAgenda.ponente"/>
          </div>

          <div v-if="!edit" class="form-group">
            <button type="button" class="btn blue" @click="agregarPreinscrito()">Agregar</button>
            <button type="button" class="btn yellow" @click="cancelarAgregar()">Cancelar</button>
          </div>
          <div v-else>
            <button type="button" class="btn blue" @click="editarPreinscrito(datosAgenda.key)">Editar</button>
            <button type="button" class="btn yellow" @click="cancelarEditar()">Cancelar</button>
          </div>
        </form>
      </div>
    </div>

    <div class="card" v-else>
      <div class="card-header">
          <h3 class="cad-title text-center">Agenda </h3>
      </div>
      <div class="card-body">

          <table class="table table-striped responsive-table">
            <thead>
              <tr>
                <th>Nombre</th>
                <th>Dia </th>
                <th> Hora</th>
                <th>lugar</th>
                <th>Ponente</th>
                <th>Acción</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="agenda of datosAgenda">
                <td>
                  <p>{{ agenda.nombre }}</p>
                </td>
                <td>
                  <p>{{ agenda.dia }}</p>
                </td>
                <td>
                  <p>
                   {{ agenda.hora }}
                  </p>
                </td>
                <td>
                  <p>{{ agenda.lugar }}</p>
                </td>
                <td>
                  <p>{{ agenda.ponente }}</p>
                </td>
                <td>
                  <div>
                    <button class="btn btn-floating blue" @click="editar(agenda)">
                      <i class="fa fa-pencil-square-o" aria-hidden="true"></i> Editar
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
      </div>
    </div>
    <v-dialog/>

  </div>

  </div>
</template>

<script>
import { programacionRef } from './firebaseInit';
import toastr from "toastr";
import VModal from 'vue-js-modal';
import { db } from './firebaseInit';


const base = db;

export default {
  name: 'ViewAgenda',

  data() {
    return {
      datosAgenda: [],
      llave:  "",
      edit: false,
      listar: true
    };
  },
  beforeRouteEnter(to, from, next) {

    var ref = base.ref('programacion');
    let datos = [];

    ref.orderByChild("evento").on("child_added", function(snapshot) {
      if(snapshot.key == to.params.evento_id){
        snapshot.val().agenda.forEach(function(value, key, myMap) {
                        // Do something
          var obj = {
                key: key,
                nombre: value.nombre,
                dia: value.dia,
                hora: value.hora,
                lugar: value.lugar,
                ponente: value.ponente,
          }
          datos.push(obj);
        });
          next(vm => {
            vm.datosAgenda = datos
          })
      }
    })
  },
  watch: {
    $route: 'fetchData'
  },
  methods: {

    editar(datos) {

      this.edit = true;
      this.listar = false;
      this.datosAgenda.key = datos.key;
      this.datosAgenda.descripcion = datos.descripcion;
      this.datosAgenda.lugar = datos.lugar;
      this.datosAgenda.nombre = datos.nombre;
      this.datosAgenda.ponente = datos.ponente;
    },

    editarPreinscrito(key) {

      const programacionRef = db.ref('programacion/' + this.$route.params.evento_id +  '/agenda/' );

      programacionRef.child(this.datosAgenda.key).update({
          lugar: this.datosAgenda.lugar,
          nombre: this.datosAgenda.nombre,
          ponente: this.datosAgenda.ponente
      });

      this.datosAgenda.nombres = "";
      this.datosAgenda.datosAgenda = "";
      this.datosAgenda.datosAgenda = "";
      this.datosAgenda.key = "";

      toastr.success("Datos actualizados correctamente.");
      this.edit = false;
      this.listar = true;
      this.$router.go({ path: this.$router.path });

    },

    cancelarEditar(){
      this.edit = false;
      this.listar = true;
    }

  }
};
</script>



