<template>

  <nav class="navbar navbar-expand-lg navbar-dark bg-primary">
  <a class="navbar-brand" href="/">
      <br>
      <img alt="Logo" width="150px" height="100px" src="../assets/logo.png"/>
  </a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
  <div class="collapse navbar-collapse" id="navbarText">
    <ul class="navbar-nav mr-auto">
        <li v-if="isLoggedIn"><router-link to="/preinscritos">Administrar preinscritos</router-link></li>
        <li v-if="isLoggedIn"><router-link to="/salas">Administrar salas</router-link></li>
        <li v-if="isLoggedIn"><router-link to="/programacion">Administrar programación</router-link></li>
    </ul>
    <span v-if="isLoggedIn" class="navbar-text">
      <span class="email white-text">Bienvenido, {{currentUser}} </span>
      <button v-on:click="logout" class="btn black">Cerrar sesión</button>
    </span>
    <span v-if="!isLoggedIn" class="navbar-text">
      <button class="btn black"> <router-link to="/login">Iniciar sesión</router-link> </button>
    </span>
  </div>
</nav>

</template>

<script>
import firebase from "firebase";

export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    };
  },
  ready: function() {
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    }
  }
};
</script>

<style scoped>
.email {
  padding-right: 10px;
}
</style>



